@import '../styles/_colors.sass';

fieldset {
	border: none;
	padding: 0;
	margin-bottom: .75rem;
	display: flex;
	legend {
		text-align: center;
		font-weight: 600;
		margin-bottom: 1rem; } }

.materialFormItem {
	position: relative;
	margin: 1rem .5rem 1rem .5rem;
	&:after {
		height: 2px;
		background-color: #cecfcf;
		width: 100%; }
	&:before {
		height: 2px;
		background: $primary-light-color;
		z-index: 1;
		width: 0;
		transition: width .3s; }
	&:after, &:before {
		display: block;
		content: '';
		position: absolute;
		bottom: 0; }
	&.error {
		margin-bottom: 36px;
		&:before {
			background-color: $error; } }
	&.focused {
		&:before {
			width: 100%; } } }

.error small.error {
	position: absolute;
	background: transparent;
	color: $error;
	font-style: normal;
	padding-left: 0; }

.materialFormLabel {
	display: block;
	width: 100%;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	line-height: 1;
	cursor: text;
	color: $primary-light-color;
	transition: transform ease .3s, color ease .3s;
	transform-origin: 0 0;
	.error & {
		color: $error; }
	.focused & {
		transform: translateY(-220%) scale(.9);
		color: $primary-light-color; }
	.error.focused & {
		color: $error; }
	.hasValue & {
		transform: translateY(-220%) scale(.9); } }

input, textarea, select {
	&.materialFormField {
		box-sizing: border-box;
		background-color: #fff;
		border: 0;
		outline: 0;
		width: 100%;
		border-width: 0;
		padding-left: 0;
		padding-right: 0;
		box-shadow: none;
		margin: 0;
		height: 2rem;
		color: #3e3e3e;
		font-size: 16px;
		font-weight: 500;
		appearance: none;
		&:focus {
			box-shadow: none;
			background: none; } } }

.button {
	background-color: $primary-light-color;
	flex: 1 1 0;
	margin: .5rem;
	border: none;
	line-height: 2.5;
	font-size: 1rem;
	color: white; }
